<template>
  <div>
    <!-- 顶部 -->
    <van-nav-bar title="确认订单" left-arrow @click-left="revert" />
    <!-- 没有收货地址 -->
    <div class="confirmOrderAddress" v-show="bool" @click="$router.push('/receivingAddress')">
      <i class="icon-left"></i>
      <p class="address">请填写收货地址</p>
      <i class="icon-right"></i>
    </div>
    <!-- 收货地址 -->
    <van-address-list
      class="van-address-list-box"
      :list="list"
      @edit="onEdit"
      @select="$router.push('/receivingAddress')"
    />
    <!-- 商品列表 -->
    <div class="shopping" v-for="(item,index) in selectedList" :key="index">
      <van-card
        :num="item.num"
        :price="item.price"
        desc="描述信息"
        :title="item.name"
        :thumb="item.img"
      />
    </div>
    <!-- 配置 -->
    <van-cell-group>
      <van-cell class="cellbox" title="配送方式" value="快递免邮" />
      <van-cell class="cellbox" title="配送时间" value="24:00前付款，预计3月13日到达" />

      <van-cell class="cellbox cellbox1" title="运费险">
        ￥{{ 1.70}}
        <div class="checkBox">
          <van-checkbox v-model="checked" checked-color="#fc5031"></van-checkbox>
        </div>
      </van-cell>
    </van-cell-group>

    <!-- 优惠卷 -->
    <van-coupon-cell
      class="coupon"
      :coupons="coupons"
      :chosen-coupon="chosenCoupon"
      @click="showList = true"
    />
    <van-popup v-model="showList" round position="bottom" style="height: 90%; padding-top: 4px;">
      <van-coupon-list
        :coupons="coupons"
        :disabled-coupons="disabledCoupons"
        :chosen-coupon="chosenCoupon"
        @change="onChange"
        @exchange="onExchange"
      />
    </van-popup>

    <van-submit-bar :price="total" button-text="提交订单" @submit="payment" />
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      coupons: [], //可用的优惠卷
      chosenCoupon: -1,
      showList: false,
      disabledCoupons: [], //不可用的优惠卷
      list: [],
      selectedList: [],
      checked: false,
      bool: false,
      total: 0,
      oldIndex: 0,
      index: null
    };
  },
  methods: {
    revert() {
      this.$store.state.bool = true;
      this.$router.go(-1);
    },
    onEdit(item, index) {
      this.$store.state.address.modifyIndex = index;
      this.$router.push("/modifyAddress");
    },
    onChange(index) {
      // console.log(index);
      this.index = index;
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(coupon) {
      let list = this.$store.state.coilingCodeList;
      list.forEach(item => {
        if (item.coilingCode == coupon && item.stact) {
          Toast("领取成功");
          this.$store.state.myjp.useCoupon.push(item);
          item.stact = false;

          let price = this.checked
            ? (this.$store.getters["shoppingcart/total"] + 170) / 100
            : this.$store.getters["shoppingcart/total"] / 100;

          this.coupons = this.$store.state.myjp.useCoupon.filter(
            item => item.apply < price && item.bool
          );

          //不可用的优惠卷
          this.disabledCoupons = this.$store.state.myjp.useCoupon.filter(
            item => item.apply > price || item.bool == false
          );
          return;
        }
        if (item.coilingCode == coupon && !item.stact) {
          Toast("已经领取过了");
        }
        if (item.coilingCode !== coupon) {
          Toast("您输入的卷码有误");
          coupon = "";
        }
      });
    },
    payment() {
      //没有填收货地址
      if (this.list.length < 1) {
        Toast("请输入收货地址");
        return;
      } else {
        this.$store.state.shoppingcart.list = [];
        this.$router.push("/payment");
        this.selectedList.forEach(item => {
          this.$store.state.myjp.allOrders.push(item);
        });
        // console.log(this.index);
        if (this.index != null) {
          this.coupons[this.index].bool = false;
        }
      }
    }
  },
  watch: {
    chosenCoupon(newVal, oldVal) {
      
      if (oldVal != -1) {
        this.total += this.coupons[this.oldIndex].value;
      }
      this.oldIndex = newVal;
      if (newVal==-1) {
        this.index = null;
        // console.log(this.oldIndex );
        return
      }
      this.total = this.total - this.coupons[newVal].value;
    },
    checked(newVal) {
      if (newVal) {
        this.total += 170;
      } else {
        this.total -= 170;
      }
    }
  },
  created() {
    this.$store.state.bool = false;
    
    if (this.$store.state.address.list.length == 1) {
      this.list = this.$store.state.address.list;
    } else if (this.$store.state.address.list.length > 1) {
      this.list = this.$store.state.address.list.filter(item => {
        return item.isDefault;
      });
    } else {
      this.bool = true;
    }

    this.selectedList = this.$store.state.shoppingcart.list.filter(item => {
      return item.bool;
    });
    let price = this.checked
      ? (this.$store.getters["shoppingcart/total"] + 170) / 100
      : this.$store.getters["shoppingcart/total"] / 100;
    // console.log((this.$store.getters["shoppingcart/total"] + 170)/100);

    // 可用的优惠卷
    this.coupons = this.$store.state.myjp.useCoupon.filter(
      item => item.apply < price && item.bool
    );

    //不可用的优惠卷
    this.disabledCoupons = this.$store.state.myjp.useCoupon.filter(
      item => item.apply > price || item.bool == false
    );
    // console.log(this.coupons, this.disabledCoupons);
    // console.log(this.$store.state.myjp.useCoupon);

    this.disabledCoupons.forEach(item => {
      if (item.bool == false) {
        item.reason = "已经使用过了";
      }
    });

    this.total = this.$store.getters["shoppingcart/total"];
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
    this.$store.state.myjp.total = this.total / 100;

    this.$store.state.myjp.useCoupon = this.coupons;
    this.disabledCoupons.forEach(item => {
      this.$store.state.myjp.useCoupon.push(item);
    });
  }
};
</script>

<style lang="scss" scoped>
.van-button--block {
  display: none;
}
.van-address-list-box {
  padding-bottom: 20px;
}
.cellbox {
  padding: 10px 50px;
  .van-cell__value {
    flex: 2;
  }
}
.checkBox {
  position: absolute;
  right: 0;
  top: 2px;
}
.cellbox1 {
  color: #000;
  .van-cell__value {
    padding-right: 24px;
  }
}
.confirmOrderAddress {
  position: relative;
  p {
    margin: 0;
    font-size: 14px;
    box-sizing: border-box;
    background-color: #48505a;
    color: #fff;
    padding: 17px 12px 17px 40px;
  }
  .icon-left {
    position: absolute;
    display: inline-block;
    width: 18px;
    height: 23px;
    background: url("https://jp.juancdn.com/jpwebapp_v1/images_v1/shopping/shouhuo-address.png")
      no-repeat 50%;
    background-size: cover;
    left: 12px;
    top: 30%;
  }
  .icon-right {
    position: absolute;
    display: inline-block;
    width: 12px;
    height: 8px;
    background: url("https://jp.juancdn.com/jpwebapp_v1/images_v1/shopping/white-arrow.png")
      no-repeat 50%;
    background-size: cover;
    transform: rotate(-90deg);
    right: 12px;
    top: 40%;
  }
}
.coupon {
  padding-left: 50px;
  padding-right: 50px;
}
</style>